import React, {Component} from 'react';
import {
	Image
} from 'react-native';
import helper from 'assets/helper';

export default class Icon extends Component {
	render () {
		const {
			size,
			color,
			name
		} = this.props;
		let uri = helper.site_url +'/icons/'+name+'.png'
		return <Image resizeMode="center" source={{uri}} style={{width:size, height:size, tintColor:color}} />
	} 
}

Icon.defaultProps = {
	color:helper.grey
}