import React, {Component} from 'react';
import {
	TouchableOpacity,
	Text,
	View
} from 'react-native';
import helper from 'assets/helper';

const tabs = [
 {name:'H\nO\nM\nE', icon:'home'},
 {name:'A\nB\nO\nU\nT', icon:'about'},
 {name:'W\nO\nR\nK\nS', icon:'works'},
 {name:'C\nO\nN\nT\nA\nC\nT', icon:'phone'}
]

export default class SideBar extends Component {
	constructor(props) {
		super(props)
		this.state = {
			current:0
		}
	}
	handleChange = (current) => {
		this.setState({current}, () => {
			this.props.onChange(current)
		})
	}
	render () {
		return (
			<View style={s.main}>
			 {tabs.map((tab, key) => {
			 	const isShowing = this.state.current === key
			 	const color = isShowing ? helper.black : helper.silver;
			 	const bg = isShowing ? helper.primaryColor : helper.grey;
		 		return(
		 			<TouchableOpacity onPress={() => this.handleChange(key)} key={key} style={[s.tab, {backgroundColor:bg}]}>
		 			 <Text style={[s.text, {color}]}>{tab.name}</Text>
		 			</TouchableOpacity>
		 		)
			 })}
			</View>
		)
	}
}

const s = {
	main :{
		height:helper.height,
		width:70,
		backgroundColor:helper.black,
		borderRightWidth:3,
		borderColor:helper.primaryColor
	},
	tab:{
		width:67,
		paddingVertical:20,
		marginBottom:10,
		justifyContent:'center',
		alignItems:'center'
	},
	text:{
		fontSize:20,
		fontWeight:'bold',
		textAlign:'center'
	}
}