export default class MainScreen extends Component {
	constructor(props){
		super(props)
		this.state = {
			currentPage:0
		}
		this.pages = [];
	}
	handleChange = (index) => {
		this.pages[this.state.currentPage].fadeOut(() => {
			this.setState({currentPage:index}, () => {
				this.pages[index].fadeIn();
			})
		});		
	}
	render () {
		return (
			<View style={s.main}>
				<SideBar onChange={this.handleChange} />
				{this.renderPage()}				
			</View>

		)
	}

	renderPage () {
		const index = this.state.currentPage;
		switch(index) {
			case 0:
			return <Home ref={ref => this.pages[0] = ref} />;
			case 1:
			return <About ref={ref => this.pages[1] = ref} />;
			case 2:
			return <Work ref={ref => this.pages[2] = ref} />;
			case 3:
			return <Contact ref={ref => this.pages[3] = ref} />;
			default:
			return null
		}
	}
}

class Work extends Component {
	constructor(props){
		super(props)
		this.state = {
			appList:[],
			currentWork:{
				techStack:[],
				images:[]
			},
			busy:true
		}
	}

	componentDidMount () {		
		this.fetchData();
	}

	fetchData = () => {
		fetch(dataURL)
		.then(response => response.json())
		.then(appList => {
			this.setState({appList}, () => {
				this.setState({currentWork:appList[0]})
			})
			this.setState({busy:false})
		}).catch((err) => {
			cogoToast.error('There was an error!')
			this.setState({busy:false})
		})
	}

	fadeIn = (cb) => {
		if(cb !== undefined)cb()
	}

	fadeOut = (cb) => {
		if(cb !== undefined)cb()
	}
	render () {
		const {
			currentWork,
			appList,
			busy
		} = this.state;
		return (
			<Animated.View style={s.content}>
			 <View style={s.workWrapper}>
				 <View style={s.appView}>
				  
				  <View style={s.appHead}>
				   <Image source={{uri:currentWork.logo}} style={s.wrkImg} />
				   <View style={s.appDesc}>
				    <Text style={s.appTitle}>{currentWork.name}</Text>
				    <Text style={s.appAbout}>{currentWork.desc}</Text>
				     <View style={s.techFlow}>
				     {currentWork.techStack.map((data) => {
				    	return (
				    		<View style={[s.techCard, {backgroundColor:data.color}]}>
				    			<Text style={s.tectTxt}>{data.name}</Text>
				    		</View>
				    	)
 				     })}
				    </View>
				   </View>
				  </View>
				  <ScrollView horizontal><View style={s.appSlider}>
				   {currentWork.images.map((img) => {
				   	return (
				   		<Image source={{uri:img}} style={s.appImg} />
				   	)
				   })}
				  </View></ScrollView>
				 </View>
				 <View style={s.appListWrapper}>
				  {appList.map(data => {
				  	return (
				  		<TouchableOpacity onPress={() => this.setState({currentWork:data})} style={s.appChip}>
				  			<Image style={s.appIcnImg} source={{uri:data.logo}} />
				  			<View style={{flex:1,padding:10}}>
					  			<Text style={s.appListTitle}>{data.name}</Text>
				  			</View>
				  		</TouchableOpacity>
				  	)
				  })}				 
				 </View>
			 </View>
			 {busy ?
			 	<View style={s.modal}>
			 	 <ActivityIndicator color={helper.primaryColor} size={50} />
			 	</View>
			 : null}
			</Animated.View>
		)
	}
}

