class Feature extends Component {
	render () {
		const {
			icon,
			name,
			desc
		} = this.props.data;
		return (
			<View style={s.item.main}>
			 <Icon name={icon} size={50} color={helper.primaryColor} />
			 <View style={s.item.other}>
				 <Text style={s.item.title}>{name}</Text>
				 <Text style={s.item.desc}>{desc}</Text>
			 </View>
			</View>
		)
	}
}

