import React, {Component} from 'react';
import {
	TouchableOpacity,
	View
} from 'react-native';
import Icon from './icon';
import helper from 'assets/helper';

const tabs = [
 {icon:'home'},
 {icon:'about'},
 {icon:'work'},
 {icon:'phone'}
]

export default class SideBar extends Component {
	constructor(props) {
		super(props)
		this.state = {
			current:0
		}
	}
	handleChange = (current) => {
		this.setState({current}, () => {
			this.props.onChange(current)
		})
	}
	render () {
		return (
			<View style={s.main}>
			 {tabs.map((tab, key) => {
			 	const isShowing = this.state.current === key
			 	const bg = isShowing ? helper.primaryColor : helper.grey;
		 		return(
		 			<TouchableOpacity onPress={() => this.handleChange(key)} key={key} style={s.tab}>
		 			 <Icon name={tab.icon} color={bg} size={25} />
		 			</TouchableOpacity>
		 		)
			 })}
			</View>
		)
	}
}

const s = {
	main :{
		height:50,
		width:helper.width,
		backgroundColor:helper.black,
		borderTopWidth:1,
		borderColor:helper.primaryColor,
		flexDirection:'row',
		justifyContent:'space-around'
	},
	tab:{
		width:50,
		height:50,
		marginBottom:10,
		justifyContent:'center',
		alignItems:'center'
	}
}